:root{
    --createColor: #ebfffc;
    --textColor: #00947e;
}
.Button__btn{
    padding: 0.5em 1em;
    background-color: var(--createColor);
    color: var(--textColor);
    border-color: transparent;
    text-align: center;
    border: 4px;
    box-shadow: none;
    font-size: 1rem;
}